import React from 'react';
import '../css/About.css';
import photoAbout from '../images/About/AboutMe.jpg';
import ownerPhoto from '../images/About/AboutMeKamila&Laura.jpg';
import { Link } from 'react-router-dom';

const AboutPage = () => {
    return (
        <div className="about navScrollToPage">
            <div className="photos">
                <div className="ownerPhoto">
                    <img src={ownerPhoto} alt="page owner Kamila" />
                </div>
                <div className="rightPhoto">
                    <img src={photoAbout} alt="little girl in the grass" />
                </div>
            </div>
            <div className="welcome">
                <h1>Hej! Mio mi Ci widzie!</h1>
                <p>Mam na imi Kamila, uwielbiam fotografi i zawsze staram sie opowiada histori moimi zdjciami..., uwiecznia momenty, ktre bdzie mio wspomina zarwno teraz jak i po latach. Po prostu utrwalam to co najpikniejsze.</p>
                <br></br>
                <p>Zapraszam do obejrzenia mojej galerii.</p>
                <button>
                    <Link to="/galerie">Galeria</Link>
                </button>
            </div>
        </div>
    );
}

export default AboutPage;